  'faq.tag': 'ꯑꯦ. ꯑꯦ. ꯀ. ꯌꯨ.',
  'faq.title': 'প্রায়শই জিজ্ঞাসিত প্রশ্ন (FAQ)',
  'faq.subtitle': 'Saubh.Tech সম্পর্কে সাধারণ প্রশ্নের উত্তর খুঁজুন',
  'faq.q1': 'Saubh.Tech কি নিরাপদ?',
  'faq.a1': 'Saubh-এ, আমাদের সর্বোচ্চ অগ্রাধিকার নিরাপত্তা এবং নিরাপত্তা। আমরা আপনার ব্যক্তিগত তথ্য সুরক্ষিত করতে সর্বশেষ এনক্রিপশন প্রযুক্তি ব্যবহার করি। আমাদের কাছে বিশেষজ্ঞদের একটি দলও রয়েছে যারা জালিয়াতি এবং অন্যান্য ক্ষতিকারক কার্যকলাপ প্রতিরোধ করতে আমাদের প্ল্যাটফর্ম 24/7 নিরীক্ষণ করে।',
  'faq.q2': 'Saubh.Tech কেন ব্যবহার করবেন?',
  'faq.a2': 'Saubh হল বিশ্বস্ত পরিষেবা প্রদানকারীদের সাথে সংযোগ করার, নিখুঁত গিগ-ওয়ার্ক খুঁজে পেতে বা অন্যান্য পেশাদারদের কাছ থেকে পরামর্শ পাওয়ার উপযুক্ত জায়গা। আপনাকে সফল করতে সাহায্য করার জন্য আমাদের কাছে বিভিন্ন ধরণের বৈশিষ্ট্য এবং সংস্থান রয়েছে এবং আমাদের দল আপনাকে সমর্থন করার জন্য সর্বদা এখানে রয়েছে।',
  'faq.q3': 'Saubh.Tech কিভাবে কাজ করে?',
  'faq.a3': 'Saubh আপনার প্রয়োজনের জন্য সঠিক লোকেদের সাথে আপনাকে সংযোগ করে। আপনি চাকরি, পরিষেবা প্রদানকারী বা শুধুমাত্র কিছু পরামর্শ খুঁজছেন না কেন, আপনি যা খুঁজছেন তা খুঁজে পেতে আমরা আপনাকে সাহায্য করতে পারি। আমাদের প্ল্যাটফর্ম ব্যবহার করা সহজ এবং আমাদের দল আপনাকে সমর্থন করার জন্য সর্বদা এখানে রয়েছে।',
  'faq.viewAll': 'FAQs & Knowledge Base-এ যান',

